<template>
  <b-card :header="$t('Store_Advertisement')">
    <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" @click="imgupload=null,$refs.addStoreAdvModel.show(),clearObjects()" class="mr-1 mb-2 mt-xl-n2 pull-right">{{$t('Add_Store_Advertisement')}}</b-button>

    <b-table :dark="dark" :sort-by.sync="sortBy" :hover="hover" :striped="striped"  :bordered="bordered" :small="small" :fixed="fixed" responsive="lg" :items="gridData" :fields="captions" :current-page="currentPage" :per-page="perPage">
      <template slot="actions" slot-scope="data">
          <a v-on:click="clearObjects(),showLoader=false,editUser=true,selectObject(data.item), $refs.editStoreAdvModel.show()" v-b-tooltip.hover :title="$t('Update')" class="icon mr-2 anchor-pointer"><i id="editIcon"  class="fa font-2x2 fa-pencil"></i></a>
          <a v-on:click="clearObjects(),showLoader=false,selectObject(data.item), $refs.deleteStoreAdvModel.show()" v-b-tooltip.hover :title="$t('Delete')"  class="icon anchor-pointer" ><i class="cui-trash font-2x2 icons mt-1"></i></a>
      </template>
      <template slot="image" slot-scope="props">
    <b-img  thumbnail :src="ApiEndPoints+'images/'+props.item.StoreImage"  rounded="left" style="height:100px;width:100px;z-index: 10;" ></b-img>
        </template>
        <template slot="active" slot-scope="status">
          <label v-if="status.item.active=='Active'" style="color: #008000;" ><b>{{status.item.active}}</b></label>
          <label  v-if="status.item.active!='Active'" style="color: #ef402b;"><b>{{status.item.active}}</b></label>
      <!-- <b-img  thumbnail :src="'https://toutpaie.kikundigital.com/images/'+props.item.StoreImage"  rounded="left" style="height:100px;width:100px;z-index: 10;" ></b-img> -->
        </template>
    </b-table>
    <div class="pt-1 pb-4" v-if="gridData.length == 0">
      <!-- <b-button style="background-color: #e4e5e6" class="w-100">{{$t('No Records Found.')}}</b-button> -->
      <div class="text-center">
      <b-spinner variant="primary" label="Text Centered"></b-spinner>
          </div>
    </div>
    <nav>
      <b-pagination class="pull-right" :total-rows="totalRows" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
    </nav>

    <b-modal no-close-on-backdrop ref="deleteStoreAdvModel" :title="$t('Delete_Store_Advertisement')" class="modal-primary">
      <p>{{$t('Are_you_sure_you_want_to_Delete_Store_Advertisement')}} <b style="color: #ef402b"></b> ?</p>
      <div slot="modal-footer" class="w-100">
        <b-button type="button" v-on:click="$refs.deleteStoreAdvModel.hide(),clearObjects()" class="float-right ml-2 "><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        <b-button type="button" style="background-color: #ef402b;border-color: #C33421;color: white" v-on:click="deleteItem($refs.deleteStoreAdvModel,selectedStoreAdv),clearObjects()" class="float-right ml-2 " variant="primary"><i class="fa fa-dot-circle-o"></i> {{$t('Confirm')}}</b-button>
      </div>
    </b-modal>

    <b-modal no-close-on-backdrop ref="addStoreAdvModel" size="lg" hide-footer :title="$t('Add_Store_Advertisement')" class="modal-primary">
      <b-form @submit.stop.prevent="showLoader=true,saveItem($refs.addStoreAdvModel, StoreAdv)">
        <b-row>
         <b-col md="6"> 
            <b-form-group validated
              :label="$t('Select_store')">
              <b-input-group>
                <b-form-select v-model="StoreAdv.StoreID" :v-on:change="changeStore(StoreAdv.StoreID)" required>
                  <option :value="null" disabled>{{$t('Please_select_store')}}</option>
                  <option v-for="item in storeList" :value="item.StoreID" v-bind:key="item.StoreID">{{item.StoreName}}</option>                              
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>
        <!-- <b-col md="6">
          <b-form-group validated
              :label="$t('Store_Img_Url')">
            <b-form-input type="text" required v-model="StoreAdv.StoreURL" :placeholder="$t('Please_Enter_Store_URL')"></b-form-input>
          </b-form-group>
        </b-col> -->
         <b-col md="6">
                  <b-form-group
                    :label="$t('Select_Department')" validated>
                    <b-form-select v-model="StoreAdv.DepartmentID"   >
                        <option :value="null" >{{$t('Physical_Store_Does_not_exist')}}</option>
                        <option v-for="item in departmentList" :value="item.DepartmentID" v-bind:key="item.DepartmentID">{{item.DepartmentName}}</option>
                    </b-form-select>
                  </b-form-group>
          </b-col>
     </b-row>
     <b-row>
       <b-col md="6">
          <b-form-group validated
              :label="$t('Store_Img')">
            <!-- <b-form-file
                v-model="StoreAdv.StoreImage"
                :state="Boolean(StoreAdv.StoreImage)"
                :placeholder="$t('Enter_Store_Image')"
                :drop-placeholder="$t('Drop_file_here')"
                required
              /> -->
             
          </b-form-group>

                  <div>   
                  <div v-if="!imgupload">
                    <input type="file" @change="onFileChange" >
                  </div>
                  <div v-if="imgupload">
                      <img :src="imgupload" style="height: 30vh; width:30vh; margin-bottom: 10px;"  />
                      <button @click="removeImage">Remove image</button>
                  </div>
              </div>
        </b-col>
     </b-row>  
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button type="submit" class="ml-2" v-if="showLoader==false" style="background-color: #ef402b;border-color: #C33421;color: white"><i class="fa fa-dot-circle-o"></i> {{$t('Save')}}</b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button type="button" class="ml-2" v-on:click="$refs.addStoreAdvModel.hide()" ><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        </div>
      </b-form>
    </b-modal>







  





































   <b-modal no-close-on-backdrop ref="editStoreAdvModel" size="lg" hide-footer :title="$t('edit_Store_Advertisement')" class="modal-primary">
      <b-form @submit.stop.prevent="showLoader=true,updateItem($refs.editStoreAdvModel, editAdv)">
        <b-row>
        <!-- <b-col md="6">
          <b-form-group validated
              :label="$t('Store_Img_Url')">
            <b-form-input type="text" required v-model="StoreAdv.StoreURL" :placeholder="$t('Please_Enter_Store_URL')"></b-form-input>
          </b-form-group>
        </b-col> -->
         <!-- <b-col md="6">
             <b-form-group
              :label="$t('Store_Img')">
           <b-img  thumbnail :src="editAdv.StoreImagePath"  rounded="left" style="height: 20vh; width:30vh"   alt="Upload Profile Photo"></b-img>
              </b-form-group>
          </b-col>
        </b-row>
         <b-row> -->

           <b-col md="6"> 
            <b-form-group validated
              :label="$t('Select_store')">
              <b-input-group>
                <b-form-select v-model="editAdv.StoreID" v-on:c="changeStore(editAdv.StoreID)" required>
                  <option :value="null" disabled>{{$t('Please_select_store')}}</option>
                  <option v-for="item in storeList" :value="item.StoreID" v-bind:key="item.StoreID">{{item.StoreName}}</option>                              
                </b-form-select>
              </b-input-group>
            </b-form-group>
          </b-col>
         <b-col md="6">
                  <b-form-group
                    :label="$t('Select_Department')" validated>
                    <b-form-select v-model="editAdv.DepartmentID"  required >
                        <option value="null" >{{$t('Please_select_department')}}</option>
                        <option v-for="item in departmentList" :value="item.DepartmentID" v-bind:key="item.DepartmentID">{{item.DepartmentName}}</option>
                    </b-form-select>
                  </b-form-group>
          </b-col>
     </b-row>
     <b-row>
        <b-col md="6">
          <b-form-group 
              :label="$t('Store_Img')">
            <!-- <b-form-file
                v-model="imgupload"
                :state="Boolean(editAdv.StoreImage)"
                 @change="onFileChange"
                 id="myFile"
                :placeholder="$t((editAdv.StoreImage ? editAdv.StoreImage : 'Enter_Store_Image'))"               
                :drop-placeholder="$t('Drop_file_here')"
                
              />-->   
          </b-form-group> 
        <div>   
          <div v-if="!imgupload">
            <input type="file" @change="onFileChange" >
          </div>
          <div v-if="imgupload">
              <img :src="imgupload" style="height: 30vh; width:30vh; margin-bottom: 10px;"  />
              <button @click="removeImage">Remove image</button>
          </div>
      </div>
        </b-col>
     </b-row> 
        <hr style="margin: 20px -20px;" />
        <div slot="modal-footer" class="w-100 text-right">
          <b-button type="submit" class="ml-2" v-if="showLoader==false" style="background-color: #ef402b;border-color: #C33421;color: white"><i class="fa fa-dot-circle-o"></i> {{$t('Save')}}</b-button>
          <b-button variant="primary" v-if="showLoader==true">
            <b-spinner small></b-spinner>
            <span class="sr-only">Loading...</span>
          </b-button> 
          <b-button type="button" class="ml-2" v-on:click="$refs.editStoreAdvModel.hide()" ><i class="fa fa-close"></i> {{$t('Cancel')}}</b-button>
        </div>
      </b-form>
    </b-modal>



  </b-card>
</template>

<script>
import VueNotifications from 'vue-notifications'
import * as toutPaieService from "./ToutPaieService.js";

export default {
  props: {
    caption: {
      type: String,
      default: 'Managers'
    },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    },
    dark: {
      type: Boolean,
      default: false
    }
  },
  data: () => {
    return {

      file: null,
      StoreAdv:{
       DepartmentID :""
      },
      
      selectedStoreAdv: {},
      saveProductGroup: {},   
       loginUser:{},
       editUser: false,
      storesList : [],
      departmentList: [],
      ApiEndPoints:"",
      sortBy : 'StoreName',
      pageOptions: [5, 10, 15],
      currentPage: 1,
      file: null,
      imgupload:"",
      editAdv:{},
      storeList:[],
      showLoader:false,
      buildingsList: [],
      perPage:10,
      StoreAdvList : [],
      otherAddr: [{
        storeName: null,
        city: null,
        pincode: null
      }],
      

    }
  },
  computed: {
    fields () {
      return [
      
        
        {key: 'image',label: this.translateString('Store_Img'),  sortable: true},
        // {key: 'StoreURL',label: this.translateString('Store_Img_Url'),  sortable: true},
        // {key: 'active',label: this.translateString('Active_Status'),  sortable: true},
         {key: 'DepartmentName',label: this.translateString('Deparment_Name'),  sortable: true},
        {key: 'actions' ,label: this.translateString('Action'),sortable:false,thStyle: {'width': '70px !important', 'min-width': '70px !important'}}
      ]
    },
    gridData: function() {
      return this.StoreAdvList;
      // Array.isArray(this.managersList) ? this.managersList : []
    },
    totalRows: function () {
      return this.getRowCount()
    },
    captions: function() {
      return this.fields
    }
  },
  methods: {
    translateString : function(colName) {
      return this.$i18n.t(colName);
    },
    getRowCount: function () {
      return this.StoreAdvList.length ;  
    },
    selectObject: function(adv){
 

        this.selectedStoreAdv = adv;
        this.editAdv=adv;
       this.editAdv.StoreImagePath=toutPaieService.ApiEndPoints+'images/'+this.editAdv.StoreImage;
        this.imgupload=toutPaieService.ApiEndPoints+'images/'+this.editAdv.StoreImage;
      //this.selectedStoreAdv = JSON.stringify(selectedStoreAdv); 
 
    },
      getDeparment: function(){
          toutPaieService.getData('departments').then(res => {
            this.departmentList = res.data;
            this.getStoreAdv();
        });
    },
    selectImage: function(img){
         console.log(img);

         var fil = document.getElementById("myFile");
        alert(fil.value);

         console.log(fil);
    },
    onFileChange(e) {

      
      var files = e.target.files || e.dataTransfer.files;
      if(this.editUser){
         this.editAdv.StoreImage=e.target.files[0];
      }else{
        this.StoreAdv.StoreImage=e.target.files[0];
      }
      
      if (!files.length)
        return;
      this.createImage(files[0]);
    },
    createImage(file) {
      var image = new Image();
      var reader = new FileReader();
      var vm = this;

      reader.onload = (e) => {
        vm.imgupload = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    removeImage: function (e) {
      this.imgupload = '';
    },

    changeStore :function (storeId) {
      
     
     for(var i=0;i<this.storeList.length;i++){
      
      if(this.storeList[i].StoreID==storeId){

         if(this.storeList[i].IsNonPhysicalStore=="Y"){
          this.StoreAdv.DepartmentID=this.storeList[i].storeDepartment[0].DepartmentID;
          console.log(storeId,"->",this.StoreAdv.DepartmentID);
          return;
          }else{
             this.StoreAdv.DepartmentID=null;
             return;
          }
      }
     } 
       
     },

      getStores: function () {      
        let url =null;   
                     url="store";
      toutPaieService.getData(url).then(res => {
        this.storeList = res.data;
      });
    },

    deleteItem: function (ref,Adv) {

      console.log('in delete function');

      let URL = 'store/adv/delete/' + Adv.StoreAdvertisementID;
     
      toutPaieService.saveData(Adv, URL).then(data => {
        console.log('data', data);

         let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") {
           this.StoreAdv={};
           this.getStoreAdv();
            this.clearObjects();
          ref.hide();
           VueNotifications.success({message: 'Store  Advertisement Deleted Successfully',timeout:3000});
           
        }else{
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }
      });


    },
    updateItem: function(ref,selectedStoreAdv){
      console.log('in update function');
      this.editUser = true;
      for(var i=0;i<this.StoreAdvList.length;i++){

        if((this.StoreAdvList[i].DepartmentID==selectedStoreAdv.DepartmentID) && (this.StoreAdvList[i].StoreAdvertisementID!=selectedStoreAdv.StoreAdvertisementID)){
              VueNotifications.error({message:this.translateString("department_allready_exits"),timeout:2500});
              this.showLoader=false;
              return;
        }

      }
      this.saveItem(ref,selectedStoreAdv);

    },
    clearObjects:function(){
     this.StoreAdv={};
     this.StoreAdv.DepartmentID=null;
     this.editAdv={};
     this.imgupload="";
     this.editUser = false;
     this.getStoreAdv();

    },
    saveItem: function(ref,StoreAdv){
      console.log('in save itom function');
        let url =null;
      
      if(this.editUser){
        url="store/adv/update/"+StoreAdv.StoreAdvertisementID;
              if(this.imgupload){
                  // StoreAdv.StoreImage=this.imgupload;
              }else{
                  StoreAdv.StoreImage="";
              }
      }else{
           url= "store/adv/save";
      }
      
         
         var form = new FormData();
         form.append("StoreImage", StoreAdv.StoreImage);
         form.append("StoreID", StoreAdv.StoreID);
        //  form.append("StoreURL",(StoreAdv.StoreURL!=null?(StoreAdv.StoreURL).includes("http")? StoreAdv.StoreURL:("https://"+StoreAdv.StoreURL):""));
          form.append("DepartmentID", StoreAdv.DepartmentID);
     toutPaieService.saveData(form, url).then(data => {
        console.log('data', data);

         let msg=data.data[0].message;
        if (data.status == 200 && msg=="Success") {
           this.StoreAdv={};
           this.imgupload="";
           this.showLoader=false;
           this.clearObjects();
           this.getStoreAdv();
          ref.hide();
           VueNotifications.success({message: this.translateString('Store_Advertisement_Successfully'),timeout:3000});
           
        }else{
          this.showLoader=false;
           VueNotifications.error({message:this.translateString(msg),timeout:2500});
        }
      });


    },
    
    getStoreAdv: function() {
			toutPaieService.getData('store/all/adv/get/adv').then(res => {
        this.StoreAdvList = res.data;

          if(this.StoreAdvList.length>0){
        for(var i=0;i< this.StoreAdvList.length;i++){
            for(var j=0;j<this.departmentList.length;j++){

               if(this.StoreAdvList[i].DepartmentID==this.departmentList[j].DepartmentID){
                  this.StoreAdvList[i].DepartmentName=this.departmentList[j].DepartmentName;
            }
               
            }
         }
        }
        console.log("StoreAdvList :",this.StoreAdvList);
        this.loginUser=JSON.parse(window.localStorage.getItem("loginUser"));
        
			});
    },
    
 
  },
  beforeMount(){

    this.ApiEndPoints=toutPaieService.ApiEndPoints;
    this.getStores();
    this.getDeparment();
    
      
  }
}
</script>